.gg-arrow-left-r {
  box-sizing: border-box;
  position: relative;
  display: block;
  width: 22px;
  height: 22px;
  border: 2px solid;
  transform: scale(var(--ggs, 1));
  border-radius: 4px;
  box-shadow: 0px 0px 200px 15px #b600ee;
}

.gg-arrow-left-r::after,
.gg-arrow-left-r::before {
  content: '';
  display: block;
  box-sizing: border-box;
  position: absolute;
  left: 4px;
}

.gg-arrow-left-r::after {
  width: 6px;
  height: 6px;
  border-bottom: 2px solid;
  border-left: 2px solid;
  transform: rotate(45deg);
  bottom: 6px;
}

.gg-arrow-left-r::before {
  width: 10px;
  height: 2px;
  bottom: 8px;
  background: currentColor;
}
input[type='file'] {
  display: none;
}
